
body.project-loaded .single.article #article-header {
  height: 350px;
  margin-top: 0px;
  opacity: 1;
}
body.project-loaded .article-content, body.project-loaded .project-content {
  opacity: 1;
}
body.project-loaded .related {
  opacity: 1;
}
.single #article-header {
  height: 0px;
  opacity: 0;
  position: relative;
  top: -75px;
  box-shadow: 0 3px 4px 0px #aaa;
  color: #fff;
  overflow: hidden;
  transition: all 0.25s ease-out;
}
.single #article-header .about {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    height: 60vh;
    min-height: 350px;
    width: 40vw;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    box-shadow: -1px 0px 245px 20px rgba(85, 85, 85, 0.56);
}
.single #article-header .about .about-content h1 {
      line-height: 25px;
      padding-bottom: 10px;
}
.single #article-header .about .about-content h5 {
      font-size: 16px;
      font-weight: normal;
      text-transform: lowercase;
}
.single #article-header .about .about-content p {
      opacity: 0.75;
}
.single #article-header .about .about-content p.description {
      padding: 30px 0;
}
.single #article-header .about .about-content p.url a {
      color: #fff;
      text-decoration: none;
}
.single.article #article-header {
  height: 0;
  min-height: 0;
  margin-top: 175px;
  opacity: 0;
  overflow: hidden;
}
.single.article #article-header .about {
    width: 100vw;
    height: 350px;
}
.single.article #article-header .about .about-content {
      width: 100%;
      max-width: 1220px;
      margin: 0 auto;
}
.single.article #article-header .about .about-content h1 {
        font-size: 46px;
        line-height: 52px;
        padding-right: 25vw;
}
.article-content {
  opacity: 0;
  padding-bottom: 60px;
  transition: all 0.25s ease-out;
}
.article-content p {
    font: 22px/28px 'Cambria', serif;
    padding: 0 10vw 15px 0;
}
.article-content p.image-subtitle {
      font-size: 16px;
      color: #999;
      margin-bottom: 30px;
}
.article-content p.original-date {
      color: #999;
      font-style: italic;
      margin: -30px 0 15px;
      font-size: 14px;
}
.article-content hr {
    margin: 0 0 30px 0;
    border: none;
}
.article-content hr:before {
    content: '...';
    text-align: center;
    width: 100%;
    display: block;
    font-family: 'Cambria',serif;
    font-weight: 400;
    font-style: italic;
    font-size: 28px;
    letter-spacing: .6em;
}
.article-content img {
    display: block;
    margin: 30px 0 0;
}
.article-content a.visite-article {
    display: block;
    width: 450px;
    text-align: center;
    padding: 15px 30px;
    margin: 0;
    background: #ccc;
    border: 2px solid #ccc;
    border-radius: 50px;
    color: #151515;
    transition: all 0.125s ease-out;
}
.article-content a.visite-article:hover {
      background: #fff;
      border-color: #151515;
}
.related {
  opacity: 0;
  transition: all 0.25s ease-out;
}
.related h1 {
    margin-bottom: 30px;
}



