
article.card {
  background: #eee;
  position: relative;
  height: 260px;
  overflow: auto;
  box-shadow: 0 2px 3px 0px #ccc;
  margin-bottom: 20px;
}
article.card figure {
    position: absolute;
    left: 0;
    top: 0;
    height: 260px;
    width: 100%;
    overflow: hidden;
}
article.card figure img {
      height: auto;
}
article.card figure img.preload {
      opacity: 0;
      display: none;
      position: absolute;
      top: -9999px;
      left: -9999px;
}
article.card .card-info {
    height: 100%;
    padding: 25px;
    color: #fff;
    margin: 0;
}
article.card .card-info a {
      color: #fff;
}
article.card .card-info h1 {
      font-size: 25px;
      line-height: 30px;
}
article.card .card-info h5 {
      font-size: 16px;
      font-weight: 300;
      margin: 12px 0;
      opacity: 0.7;
}
article.card .card-info p.description {
      font-size: 16px;
      font-weight: 200;
      line-height: 17px;
      opacity: 0.7;
}
article.card .card-info p.url {
      position: absolute;
      bottom: 20px;
}
article.card.card-article {
    background: #151515;
}
article.card.card-project .card-info {
    width: 50%;
    position: absolute;
    right: 0;
    top: 0;
}



