
body.project-loaded .single #project-header {
  height: 60vh;
  min-height: 350px;
  opacity: 1;
}
body.project-loaded .single #project-header figure img {
    -webkit-filter: blur(0px);
            filter: blur(0px);
    -webkit-transform: rotate(-2.5deg);
            transform: rotate(-2.5deg);
    width: 100%;
}
body.project-loaded .project-content {
  opacity: 1;
}
body.project-loaded .related {
  opacity: 1;
}
.single #project-header {
  height: 0px;
  opacity: 0;
  position: relative;
  top: -75px;
  box-shadow: 0 3px 4px 0px #aaa;
  color: #fff;
  overflow: hidden;
  transition: all 0.25s ease-out;
}
.single #project-header figure {
    position: absolute;
    top: 0;
    right: 0;
}
.single #project-header figure img {
      position: relative;
      top: -20px;
      left: 150px;
      transition: all 0.5s ease-out;
      -webkit-filter: blur(10px);
              filter: blur(10px);
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      width: 102%;
}
.single #project-header .about {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    height: 60vh;
    min-height: 350px;
    width: 40vw;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    box-shadow: -1px 0px 245px 20px rgba(85, 85, 85, 0.56);
}
.single #project-header .about .about-content h1 {
      line-height: 25px;
      padding-bottom: 10px;
}
.single #project-header .about .about-content h5 {
      font-size: 16px;
      font-weight: normal;
      text-transform: lowercase;
}
.single #project-header .about .about-content p {
      opacity: 0.75;
}
.single #project-header .about .about-content p.description {
      padding: 30px 0;
}
.single #project-header .about .about-content p.url a {
      color: #fff;
      text-decoration: none;
}
.single.project #project-header .about {
  padding: 0 10vw;
}
.project-content {
  opacity: 0;
  padding-bottom: 60px;
  transition: all 0.25s ease-out;
}
.project-content .explain-module {
    margin-bottom: 25px;
    overflow: auto;
}
.project-content .explain-module h3 {
      font-size: 30px;
}
.project-content .explain-module p {
      margin: 0 0 15px 0;
      font: 20px/26px 'Cambria', serif;
}
.project-content .explain-module ul {
      padding-left: 20px;
}
.project-content .explain-module ul li {
        margin-bottom: 15px;
        padding-left: 10px;
}
.related {
  opacity: 0;
  transition: all 0.25s ease-out;
}
.related h1 {
    margin-bottom: 30px;
}



